<template>
    <div   id="app1" v-bind:class="bgcolor">
        <div style="height:3vh;"></div>
        <div class="control">
            <div class="control1">
                <el-page-header @back="goBack" content="浏览页面">
                </el-page-header>

            </div>
            <div class="colorcontrol">
                <el-switch
                        v-model="value1"
                        active-text="正常模式"
                        inactive-text="夜间模式">
                </el-switch>
            </div>
            <div style="font-size:18px;position:absolute;margin-top:4vh;margin-left:50%">字体大小</div>
            <div class="controlsize">
                <el-slider v-model="value2"></el-slider>
            </div>
        </div>

        <div v-bind:class="txtcolor">
            <div class="chaptercontrol">
                <div class="chapter1"><el-button plain @click="prevchap">上一章</el-button></div>
                <div class="chapter2"><el-button plain @click="nextchap">下一章</el-button></div>
            </div>
            <h1>{{chaptername}}</h1>
            <p v-bind:style="`font-size:${value2}px`">
                {{msg}}
            </p>
            <div class="chaptercontrol">
                <div class="chapter1"><el-button plain @click="prevchap">上一章</el-button></div>
                <div class="chapter2"><el-button plain @click="nextchap">下一章</el-button></div>
            </div>
        </div>
        <div style="height:10vh;">

        </div>
    </div>
</template>

<script>
   import Global from '@/global.js';
    export default {
        name: "Bookview",
        el:"#app1",
        data(){
            return{
                bookid:Number(this.$route.query.bookid),//书的编号
                chapterid:Number(this.$route.query.chapterid),//章节编号
                value2: 20,//默认字体大小
                value1:true,
                bgcolor:"daybg",
                txtcolor:"daytxt",
                chaptermaxsize:this.$route.query.chaptermaxsize,//本书最大章节数,章节总数
                chaptername:Global.chapter[this.chapterid-1],//本章节名字
                msg:Global.story[this.chapterid-1]//章节内容

            };
        },
        mounted(){
            this.chaptername=Global.chapter[this.chapterid-1];
            this.msg=Global.story[this.chapterid-1];

        },
        beforeUpdate(){
            this.chaptername=Global.chapter[this.chapterid-1];
            this.msg=Global.story[this.chapterid-1];
            console.log(this.chaptername);
            if(this.value1){
                this.bgcolor="daybg";
                this.txtcolor="daytxt";
            }
            else{
                this.bgcolor="nightbg";
                this.txtcolor="nighttxt";
            }

        },
        methods:{
            goBack() {
                this.$router.push({
                    path:"/Bookcatalog",
                    query:{
                        bookid:this.bookid,
                    }
                });
            },
            prevchap(){
                if(this.chapterid===1){
                    alert('已经是首章，不存在上一章');
                }
                else{
                    this.chapterid=this.chapterid-1;
                    this.value2=this.value2+1;
                    this.value2=this.value2-1;
                    console.log(this.chapterid);

                }

            },
            nextchap(){
                if(this.chapterid>=this.chaptermaxsize){
                    alert("已经是最后一章，不存在下一章");
                }
                else{
                    this.chapterid=this.chapterid+1;
                    this.value2=this.value2+1;
                    this.value2=this.value2-1;
                    console.log(this.chapterid);

                }

            }
        },
    }
</script>

<style scoped>
    .chaptercontrol{
        position:relative;
        width:100%;
        height:5vh;
        margin-top:4vh;
    }
    .chapter1{
        position:absolute;
        margin-left:20%;

    }
    .chapter2{
        position:absolute;
        margin-left:70%;
    }


    .control{
        width:90%;
        margin-left:5%;
        height:12vh;
        margin-top:2vh;
        background-color: white;
        border-radius: 4px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
        position: relative;
    }
    .colorcontrol{
        position:absolute;
        margin-left:25%;
        margin-top:4vh;
    }
    .controlsize{
        position:absolute;
        margin-top:3vh;
        width:20%;
        margin-left:60%;
    }
    .control1{
        position:absolute;
        margin-top:4vh;
        margin-left:4%;
    }



    .daybg{
        width:90%;
        margin-left:5%;
        margin-top:2vh;
        background-color: #f5f5f5;
        min-height:1000px;
        border-radius: 4px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }
    .nightbg{
        width:90%;
        margin-left:5%;
        margin-top:2vh;
        background-color: #0f1112;
        min-height:1000px;
        border-radius: 4px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }
    .daytxt{
        color:black;
        width:80%;
        margin-left:10%;
    }
    .nighttxt{
        color:#626363;
        width:80%;
        margin-left:10%;
    }

</style>