<template>
    <div class="bg">
        <el-row class="book">
            <div>
                <el-page-header @back="goBack" content="作品目录" style="position:absolute;margin-top:1vh;">
                </el-page-header>
            </div>
            <div style="background-color:#C20C0C;width:100%;height:2px;margin-top:6vh;position:absolute"></div>
            <div class="bookimg">
                <img :src="require(`../assets/tmp${id}.jpg`)" height="100%" width="100%"/>
            </div>
            <div class="bookname">
                书名{{bookname}}
            </div>
            <div class="bookwriter">
                <i class="el-icon-user-solid"></i>
                作者姓名
            </div>
            <div class="bookclass">
                <el-tag class="el-tag">标签一</el-tag>
                <el-tag type="success" class="el-tag">标签二</el-tag>
                <el-tag type="info" class="el-tag">标签三</el-tag>
                <el-tag type="warning" class="el-tag">标签四</el-tag>
                <el-tag type="danger" class="el-tag">标签五</el-tag>
            </div>
            <div class="bookinfo">
                <el-tooltip  effect="dark" content="作 品 简 介" placement="bottom">
                    <p class="bookinfo1">
                        {{bookinfo}}
                    </p>
                </el-tooltip>
            </div>

            <div style="margin-left:47%;position:absolute;margin-top:72vh;font-size:20px">作品目录</div>

        </el-row >

        <el-row style="width:90%;margin-left:5%;">
            <el-col :span="8" v-for="tmp in maxchapter" :key="tmp">
                <el-divider></el-divider>
                <div class="booklist">
                    <li  @click="jump(tmp)" style="list-style-type:none;line-height:5vh;">
                        <router-link to ="" class="list1">
                            {{chapter[tmp-1]}}
                        </router-link>
                    </li>
                </div>
            </el-col>
        </el-row>
        <div style="background-color: #C20C0C;width:90%;height:2px;margin-left:5%;margin-top:10vh;"></div>
        <el-row style="height:10vh">

        </el-row>


    </div>
    
</template>

<script>
    import Global from '@/global';
    export default {
        name: "Bookcatalog",
        data(){
            return{
                id:this.$route.query.bookid,
                maxchapter:10,//最大章节数，章节总数
                chapter:[],//章节名字
                bookname:'',//书的名字
                bookinfo:''//书的简介
            };
        },
        mounted(){//初始化
            this.bookname=Global.booknames[this.id-1];
            this.maxchapter=Global.chapter.length;
            for(let i=0;i<Global.chapter.length;i++){
                this.chapter[i]=Global.chapter[i];
            }
            this.bookinfo=Global.bookinfo[this.id-1];

        },
        methods:{
            jump(t){
                this.$router.push({
                    path:"/Bookview",//进入浏览界面
                    query:{
                        chapterid:t,//传入章节编号
                        bookid:this.id,//传入书编号
                        chaptermaxsize:this.maxchapter,//传入最大章节数，总章节数
                    }
                })
            },
            goBack() {
                this.$router.push({
                    path:'/Home'
                });
            }

        }

    }
</script>

<style scoped>
    .bg{
        background-color:white;
        width:80%;
        margin-left:10%;
        min-height:100px;
        margin-top:1vh;

    }
    .book{
        width:90%;
        height:80vh;
        background-color: white;
        margin-left:5%;
        position:relative;
    }
    .bookimg{
        width:25%;
        height:50vh;
        position:absolute;
        margin-left:10%;
        margin-top:10vh;
        border-radius: 4px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .50), 0 0 6px rgba(0, 0, 0, .30);
        background-color:white;
    }
    .bookname{
        font-family:"微软雅黑 Light";
        position:absolute;
        font-size:45px;
        margin-left:40%;
        margin-top:13vh;
    }
    .bookwriter{
        position:absolute;
        margin-left:41%;
        margin-top:27vh;

    }
    .bookclass{
        position:absolute;
        margin-left:40%;
        margin-top:35vh;
    }
    .el-tag{
        margin-left:10px;
    }
    .bookinfo{
        position:absolute;
        width:50%;
        margin-left:40%;
        margin-top:43vh;
    }
    .bookinfo1{
        font-size:15px;
        font-family:"微软雅黑";
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
    }
    .booklist{
        position:relative;
        width:90%;
        margin-left:5%;
        height:5vh;
        background-color:#fafafa;
        margin-top:2vh;
        border-radius: 4px;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    }
    .list1{
        text-decoration: none;
        font-size:14px;
        color:black;
        font-family: 等线;
        margin-top:10vh;



    }


</style>