                currentPage:1,
                booknames:[],//所有书的名字
                bookinfo:[],//所有书的简介

            };
        },
        mounted(){//初始化
            // var temp=[];
            for(let i=0;i<15;i++){
                this.booknames[i]=Global.booknames[i];
                this.bookinfo[i]=Global.bookinfo[i];
            }

        },
        beforeUpdate(){//数值发生变化后初始化

            for(let i=0;i<15;i++){
                this.booknames[i]=Global.booknames[(this.currentPage-1)*15+i];
                this.bookinfo[i]=Global.bookinfo[(this.currentPage-1)*15+i];
            }

        },
        methods: {
            handleSelect(key, keyPath) {
                console.log(key, keyPath);
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
            },
            jump(t){
                this.$router.push({
                    path:"/Bookcatalog",
                    query:{
                        bookid:t,
                    }
                })
            }

        }
    }
</script>


