<template>
    <div class="bg">
        <div style="height:3vh;">
        </div>
        <el-page-header @back="goBack" content="登录页面" style="margin-top:1vh;margin-left:5%;"></el-page-header>
        <div style="background-color:#C20C0C;width:90%;height:2px;margin-top:3vh;margin-left:5%;"></div>
<!--        <div class="btn f-color-control" @click="test">登 录</div>-->
        <div class="bg1">
            <div style="background-color:#C20C0C;width:90%;height:2px;margin-top:3vh;margin-left:5%;position: absolute;"></div>
            <div class="txt1">密码登录</div>
            <div class="usrname">
                <el-input
                        placeholder="请输入邮箱或者用户名"
                        v-model="id"
                        clearable>
                </el-input>
            </div>
            <div class="usrpassword">
                <el-input placeholder="请输入密码" v-model="code" show-password></el-input>
            </div>
            <div class="loginbtn f-color-control" @click="Login">登 录</div>
            <div class="registerbtn f1-color-control" @click="Register" >注 册</div>
        </div>
    </div>
</template>

<script>
    import Global from '@/global';
    export default {
        name: "Login",
        data(){
            return{
                id:'',//输入的用户名
                code:'',//输入的密码
                usrid:'name1',   //从数据库中调出的用户名，需修改
                usrpassword:'123456',//从数据库中调出的密码，需修改

            };
        },
        methods:{
            test(){
                alert("this is a test!");
            },
            goBack(){
                this.$router.go(-1);
            },
            Login(){
                if(this.id===''){
                    alert('用户名不能为空');
                }
                else if(this.code===''){
                    alert('密码不能为空');
                }
                else if((this.id!==this.usrid)||(this.code!==this.usrpassword)){//输入的与数据库中的匹配
                    alert('用户名或密码错误');
                }
                else{//用户名和密码正确，跳转到下一个页面
                    Global.setFlag(1);//改变global.js中的flag,改变登录状态
                    Global.setName(this.id);//改变global.js中的id,改变默认的用户名
                    this.$router.push({
                        path:"/",
                    });

                }

            },
            Register(){
                this.$router.push({
                    path:"/Register",
                })
            }



        }
    }
</script>

<style scoped>
    .bg1{
        position: relative;
        width:60%;
        height:45vh;
        margin-top:10vh;
        margin-left:20%;
        background-color:#f5f5f5;
        border-radius: 10px;
        box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    }
    .txt1{
        position:absolute;
        color:black;
        font-size:15px;
        margin-left:10%;
        margin-top:5vh;
    }
    .usrname{
        position:absolute;
        width:60%;
        margin-left:10%;
        margin-top:10vh;
    }
    .usrpassword{
        position: absolute;
        width:60%;
        margin-left:10%;
        margin-top:20vh;
    }

    .loginbtn{
        width:30%;
        height:6vh;
        text-align: center;
        line-height:6vh;
        color:white;
        border-radius: 30px;
        position:absolute;
        margin-top:30vh;
        margin-left:10%;
    }
    .registerbtn{
        width:30%;
        height:6vh;
        text-align: center;
        line-height:6vh;
        color:#C20C0C;
        border-radius: 30px;
        position:absolute;
        margin-top:30vh;
        margin-left:50%;
        border:solid 1px #C20C0C;
    }

    .f1-color-control{
        transform: scale(1);
        border-radius: 30px;
        background-color:white ;
        border-color:#C20C0C;

    }
    .f1-color-control:before{
        position: absolute;
        top: 0;  right: 0;  bottom: 0;  left: 0;
        z-index:-1;
        border-radius: 30px;
        background:rgba(0,0,0,.1);
    }
    .f1-color-control:after{
        position: absolute;
        top: 0; right: 0; bottom: 0; left: 0;
        z-index:-1;
        border-radius: 30px;
        background:rgba(255,255,255,.3);
    }
    .f1-color-control:hover:before{
        content:"";
    }
    .f1-color-control:active:after{
        content:"";
    }
    .f-color-control{
        transform: scale(1);
        border-radius: 30px;
        background-color: #C20C0C;
    }
    .f-color-control:before{
        position: absolute;
        top: 0;  right: 0;  bottom: 0;  left: 0;
        z-index:-1;
        border-radius: 30px;
        background:rgba(0,0,0,.1);
    }
    .f-color-control:after{
        position: absolute;
        top: 0; right: 0; bottom: 0; left: 0;
        z-index:-1;
        border-radius: 30px;
        background:rgba(255,255,255,.3);
    }
    .f-color-control:hover:before{
        content:"";
    }
    .f-color-control:active:after{
        content:"";
    }
    .bg{
        background-color:white;
        width:80%;
        margin-left:10%;
        min-height:600px;
    }
</style>