import Vue from "vue";
import App from "./App.vue";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import VueRouter from "vue-router";
import Home from "./views/Home.vue";
import HelloWorld from "@/components/HelloWorld";
import Testvue from "@/components/test";
import Bookcatalog from "@/components/bookcatalog";
import Bookview from "@/components/bookview";
import Login from "@/components/Login";
import Register from "@/components/Register";
import axios from "axios";
Vue.use(ElementUI);
Vue.use(VueRouter);
Vue.prototype.$axios = axios;
Vue.config.productionTip = false;

let router = new VueRouter({
  mode: "history",
  routes: [
    { path: "/Home", name: "app", component: Home },
    { path: "/HelloWorld", name: "app", component: HelloWorld },
    { path: "/test", name: "Testvue", component: Testvue },
    { path: "/", redirect: "Home" },
    { path: "/Bookcatalog", name: "app", component: Bookcatalog },
    { path: "/Bookview", name: "app", component: Bookview },
    { path: "/Login", name: "app", component: Login },
    { path: "/Register", name: "app", component: Register }
  ]
});
new Vue({
  el: "#app",
  render: c => c(App),
  router: router
});
